/*
Base Colors styles.
 */
/*
Base Layout styles.
 */
@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* *::before *::after {
  box-sizing: inherit;
}

body {
  overflow: hidden;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  font-family: Spartan, sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 0;
  text-align: left;
}

/*
Typography styles.
 */
body {
  font-family: "Spartan", sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 25px;
  letter-spacing: 0;
  text-align: left;
  color: #60636D;
}

h1.big-header {
  font-family: "Spartan", sans-serif;
  font-size: 200px;
  font-style: normal;
  font-weight: 700;
  line-height: 200px;
  letter-spacing: -5px;
  text-align: left;
}

h1.medium-header {
  font-family: "Spartan", sans-serif;
  font-size: 80px;
  font-style: normal;
  font-weight: 700;
  line-height: 80px;
  letter-spacing: -2px;
  text-align: left;
}

h2 {
  font-family: "Spartan", sans-serif;
  font-size: 56px;
  font-style: normal;
  font-weight: 700;
  line-height: 56px;
  letter-spacing: -2px;
  text-align: left;
}

h3 {
  font-family: "Spartan", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 0px;
  text-align: left;
}

.white {
  color: #fff;
}

.very-light-grey {
  color: #EEEFF4;
}

.dark-blue {
  color: #1B1D23;
}

.dark-grey {
  color: #60636D;
}

.red {
  color: #DF5656;
}

@media screen and (max-width: 1300px) {
  h1.big-header {
    font-size: 120px;
  }
}
@media screen and (max-width: 768px) {
  h1.medium-header {
    font-family: "Spartan", sans-serif;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: -1.2000000477px;
    text-align: left;
  }
  h1.big-header {
    display: none;
  }
  h2 {
    font-size: 40px;
    line-height: 48px;
    letter-spacing: -1.4285714626px;
  }
}
/*
Button styles.
 */
.button1 {
  background: #1B1D23;
  font-size: 15px;
  border: none;
  color: #fff;
  width: 252px;
  height: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease-in-out;
}

.button1 svg {
  margin-left: 20px;
}

.button1:hover {
  transition: 0.3s ease-in-out;
  background: #60636D;
}

.small-button1 {
  width: 187px;
  height: 72px;
}

.small-small-button1 {
  width: 169px;
  height: 72px;
}

/*
Nav bar styles.
 */
html {
  overflow-x: hidden;
}

body {
  overflow-x: hidden;
  z-index: 0;
}

nav {
  display: flex;
  align-items: center;
  min-height: 152px;
  background-color: #fff;
  width: 100%;
  max-width: 1110px;
  margin: auto;
}

.logo {
  position: relative;
}

.nav-links {
  display: flex;
  width: 316px;
  margin-left: 86px;
  justify-content: space-between;
  width: 360px;
}

.nav-links a {
  color: #7D828F;
  font-family: "Spartan", sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 0;
  text-align: left;
  transition: all 0.2s ease-in-out;
}

.nav-links a:hover {
  color: #1B1D23;
  transition: all 0.2s ease-in-out;
}

.burger {
  display: none;
  cursor: pointer;
}

.burger div {
  width: 24px;
  height: 3px;
  margin: 5px;
  background-color: #000;
  transition: all 0.3s ease-in;
}

.dark-overlay-nav-mobile {
  background-color: rgba(0, 0, 0, 0.4980392157);
  position: absolute;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  top: 92px;
  display: none;
  z-index: 0;
}

.dark-overlay-nav-mobile {
  background-color: rgba(0, 0, 0, 0.4980392157);
  position: absolute;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  top: 92px;
  display: block;
  z-index: -1;
  opacity: 0;
  transition: all 0.3s ease-in;
}

.dark-overlay-nav-mobile.overlay-active {
  opacity: 1;
  transition: all 0.3s ease-in;
  z-index: 98;
}

@media screen and (max-width: 1300px) {
  nav {
    width: 100%;
    max-width: 85%;
  }
}
@media screen and (max-width: 768px) {
  nav {
    max-width: 89.083%;
    justify-content: space-between;
    min-height: 94px;
  }
  .nav-links {
    background-color: #EEEFF4;
    position: fixed;
    right: 0;
    height: 35.2323vh;
    top: 91.5px;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
    justify-content: center;
    width: 96%;
    margin: 0;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
    z-index: 99;
  }
  .nav-links li {
    margin: 15px 0 15px 40px;
    opacity: 1;
  }
  .nav-links a {
    color: #1B1D23;
    font-size: 18px;
  }
  .burger {
    display: block;
  }
}
.nav-active {
  transform: translateX(0%);
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
  opacity: 0;
}

.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

/*
Main container styles.
 */
.container {
  width: 1110px;
  margin: 0 auto;
  display: flex;
  flex-flow: column nowrap;
}

@media screen and (max-width: 1300px) {
