/*
Slider styles.
 */
.slider {
  width: 100%;
  height: 720px;
  overflow: hidden;
}

.slides {
  width: 500%;
  height: 500px;
  display: flex;
  position: relative;
}

.slides input {
  display: none;
}

.slide {
  width: 20%;
  transition: 1s;
}

.slide img {
  width: 100%;
  height: 720px;
  background-position: left;
}

.navigation-manual {
  position: relative;
  width: 100%;
  height: 80px;
  display: flex;
  bottom: 80px;
  left: -80px;
  justify-content: flex-start;
  color: #000;
}

.manual-btn {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  cursor: pointer;
  transition: 0.17s ease-in-out;
}

.manual-btn:hover {
  background-color: #EEEFF4;
  transition: background-color 0.17s ease-in-out;
}

#radio1:checked ~ .first {
  margin-left: 0;
}

#radio2:checked ~ .first {
  margin-left: -20%;
}

#radio3:checked ~ .first {
  margin-left: -40%;
}

#radio4:checked ~ .first {
  margin-left: -60%;
}

.navigation-auto {
  position: absolute;
  display: flex;
  width: 320px;
  height: 80px;
  background: #fff;
  bottom: -220px;
  left: -80px;
}

.navigation-auto div {
  width: 80px;
  height: 80px;
  transition: 1s;
}

.background_black {
  background: #000 !important;
  color: #fff;
  transition: 1.3s ease-in-out;
}

.slide-text {
  position: absolute;
  width: 1110px;
  height: 720px;
  background-color: rgba(0, 0, 0, 0.2470588235);
}

.slide-text-wrap {
  width: 500px;
  height: 361px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  margin: 180px 376px 180px 190px;
}

