#submit-button:hover {
  background-color: #60636D;
  transition: 0.3s ease-in-out;
}

#submit-button::before {
  content: "";
  background-image: url(../images/icons/icon-arrow-white-white.svg);
  background-size: cover;
  width: 24px;
  height: 18px;
  position: relative;
  left: 29px;
  top: 29px;
}

@media screen and (max-width: 1024px) {
  .contact-form-section {
    width: 90%;
    height: 430px;
    flex-flow: column nowrap;
  }
  .contact-form {
    width: 100%;
    height: 100%;
    position: relative;
  }
}
@media screen and (max-width: 768px) {
  .contact-form-section {
    width: 90%;
    margin: 100px auto 0 auto;
    height: 430px;
    flex-flow: column nowrap;
  }
  .contact-form {
    width: 100%;
    height: 100%;
    position: relative;
  }
}